.counter {
	margin: 5rem auto;
	box-shadow: 0 1px 8px rgba(0, 0, 0, 0.2);
	max-width: 40rem;
	border: 1px solid #ddd0eb;
	border-radius: 2px;
	padding: 1rem;
	text-align: center;
	background-color: #f4f0fa;
}

.counter h1 {
	text-transform: uppercase;
	color: #575757;
	margin: 0;
	font-size: 1rem;
	font-weight: 600;
}

.value {
	font-size: 2rem;
	color: #3c0080;
	margin: 2rem 0;
	font-weight: bold;
}

.actions {
	margin: 1rem auto;
	padding: 1rem;
	border: 1px solid #ddd0eb;
}

.actions button {
	margin: 4px;
}